<template>
    <div class="need-confirmation">

        <div class="need-confirmation-title">
            <div class="h1"
                 v-html="$t('need_confirm.title')"/>
            <p v-html="$t('need_confirm.descr')"/>
        </div>

        <div class="need-confirmation-body">
            <div class="row">

                <div class="col-6">
                    <div class="need-confirmation-form-block">
                        <form class="need-confirmation-form"
                              @submit.prevent="personSendHandler">
                            <div class="form-title">
                                <div class="h2"
                                     v-html="$t('need_confirm.employee_title')"/>
                            </div>
                            <div class="form-descr">
                                <p v-html="$t('need_confirm.employee_descr')"/>
                            </div>
                            <div class="form-group">
                                <input type="text"
                                       class="form-control no-label"
                                       placeholder="Введите телефон директора"
                                       v-model="phone"
                                       :class="{invalid: $v.phone.$error ? (!$v.phone.required || !$v.phone.minLength) : false }"
                                       @change="addCountryCode"
                                       v-mask="'+7 ### ### ## ##'">
                                <button type="submit"
                                        class="btn"
                                        :class="{'loading': load}"
                                        :disabled="load">ОТПРАВИТЬ</button>
                            </div>
                        </form>
                    </div>
                </div>

                <div class="col-6">
                    <div class="need-confirmation-form-block">
                        <div class="check-on-director">
                            <div class="h2"
                                 v-html="company.ul ? 'Я директор' : 'Я индивидуальный предприниматель'" />
                            <p v-html="$t('need_confirm.director_descr')" />
                            <button class="btn"
                                    @click="confirmAccountAdmin"
                                    :class="{'loading': load2}"
                                    :disabled="load2">Подтвердить личность</button>
                        </div>
                    </div>
                </div>

            </div>
        </div>


        <Popup v-if="successModal"
               ref="successModal"
               @close-modal="closeSuccessModal"
               classes="code-modal-popup">

            <div class="popup-title">
                <div class="h3">Ссылка на подтверждение отправлена по номеру телефона</div>
            </div>

            <div class="popup-body">

                <p>Мы отправили код для подтверждения на номер <span style="white-space: nowrap;font-weight: bold">{{phone | VMask('+# ###  ###-##-##')}}</span></p>

                <p>После подтверждения личности директора необходимо обновить страницу.</p>

                <button class="btn btn-block close-modal">Хорошо</button>

            </div>

        </Popup>

        <Popup v-if="updatePageModal"
               ref="updatePageModal"
               :noClose="true">

            <div class="popup-title">
                <div class="h3">Для входа нужно перезагрузить страницу</div>
            </div>

            <div class="popup-body">

                <p>После подтверждения учетной записи, перезагрузите страницу</p>

                <button
                    class="btn btn-block"
                    @click="reloadPage">Перезагрузить</button>

            </div>

        </Popup>

    </div>
</template>

<script>

    import {minLength, required} from "vuelidate/lib/validators";
    import {mapState} from 'vuex'
    import Popup from "@/components/Blocks/Popup";

    export default {
        name: "NeedConfirmation",
        components: {
            Popup
        },
        validations: {
            phone: {required, minLength: minLength(16)},
        },
        data: () => ({
            phone: '',
            load: false,
            load2: false,
            codeModal: false,
            codeVal: '',
            sendCodeDisabled: true,
            sendCodeLoading: false,
            successModal: false,
            checkAdminTimeout: '',
            updatePageModal: false
        }),
        computed: {
            ...mapState({
                currentUser: state => state.users.currentUser,
                company: state => state.users.company
            })
        },
        methods: {
            personSendHandler() {
                if (this.$v.$invalid) {
                    this.$v.$touch();
                    return
                }
                this.load = true
                this.$store.dispatch('sendMessageForAdmin', this.$options.filters.phoneFilter(this.phone)).then(res => {
                    if ( res ) {
                        this.successModal = true
                    }
                    this.successModal = true
                    this.load = false
                }).catch(() => this.load = false)
            },
            confirmAccountAdmin() {
                this.load2 = true
                this.$store.dispatch('sendMessageForAdmin', this.currentUser.phone).then(() => {
                    this.successModal = true
                    this.load2 = false
                }).catch(() => this.load2 = false)
            },
            startCheck() {
                // this.checkAdminTimeout = setInterval(() => {
                //     this.$store.dispatch('getCurrentUser').catch(resp => {
                //         if ( resp === 401 ) {
                //             this.$router.push('/login')
                //         }
                //     })
                // }, 1000)
            },
            closeSuccessModal() {
                this.successModal = false
                setTimeout(() => {
                    this.updatePageModal = true
                }, 3000)
                // this.startCheck()
            },
            addCountryCode(e) {
                if ( e.indexOf('+7') !== 0 && e.charAt(0) !== '9' ) {
                    this.phone = '+7'
                }
            },
            reloadPage () {
                location.reload()
            }
        },
        mounted() {

            // this.$on('hook:beforeDestroy', () => {
            //     clearInterval(this.checkAdminTimeout)
            // });

        }
    }
</script>

